<template>
    <div class="table">
        <div class="controls">
            <md-button type="link" inline @click="showSortMenu = true">排序</md-button>
            <md-button type="link" icon="sort" inline round size="large" @click="showSortMenu = true"></md-button>
        </div>
        <div class="loading" v-if="tableLoading">
            <md-activity-indicator :size="35" vertical class="md-activity-indicator-css">加载中...
            </md-activity-indicator>
        </div>
        <div class="totalList" v-else>
            <table-item v-for="(item, index) in tableData" :key="index" :data="item"></table-item>
        </div>
        <md-action-sheet
                v-model="showSortMenu"
                :title="title"
                :default-index="defaultIndex"
                :cancel-text="cancelText"
                :options="options"
                @selected="selectSort"
        ></md-action-sheet>
    </div>
</template>

<script>
    import { Toast } from 'mand-mobile'
    import tableItem from '../components/tableItem'
    export default {
        name: "table",
        components: {
            tableItem
        },
        data() {
            return {
                tableLoading: false,
                tableData: [],
                showSortMenu: false, // 排序功能菜单
                title: '选择排序功能',
                options: [
                    {
                        label: '默认排序（最新日期）'
                    },
                    {
                        label: '收入最高',
                        sortKey: 'inCount',
                        sortType: 1
                    },
                    {
                        label: '支出最高',
                        sortKey: 'outCount',
                        sortType: 1
                    },
                    {
                        label: '收入最低',
                        sortKey: 'inCount',
                        sortType: 0
                    },
                    {
                        label: '支出最低',
                        sortKey: 'outCount',
                        sortType: 0
                    },
                ],
                defaultIndex: 0,
                cancelText: '取消'
            }
        },
        methods: {
            fetchTotalData(sortKey, sortType) {
                const url = sortKey ? `/api/getTotalItems?sortKey=${sortKey}&sortType=${sortType}` : '/api/getTotalItems';
                this.tableLoading = true;
                this.$http.get(url).then(res => {
                    this.tableData = res.data.data;
                    this.tableLoading = false;
                }).catch(() => {
                    this.tableLoading = true;
                    Toast.failed('数据获取失败')
                })
            },
            getTotalData() {
                this.fetchTotalData();
            },
            // 选择排序
            selectSort(item) {
                this.fetchTotalData(item.sortKey, item.sortType);
            }
        },
        mounted() {
            this.getTotalData();
        }
    }
</script>

