            &:first-child {
                right: 0.9rem;
                bottom: 0.14rem;
            }
            /deep/.md-icon.icon-font.md {
                font-size: 0.5rem;
            }
        }
    }
    .loading .md-activity-indicator-css {
        margin-top: 40%;
        font-size: 0.4rem;
    }
